<template>
    <div>
        <loader :isVisible="isLoading"></loader>
        <section class="main-content">
            <tg-list :IsShowAction="true" ModuleName="CustomField" SubModuleCode="CustomField" :LegendArray="[]"
                :showCheckBox="true" IdentityColumn="CUSTOM_LAYOUT_ID" :listType="['List']" :ListData="FieldsData"
                :HeaderText="$t('Custom_Fields')" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData" :TotalRecords="TotalRecords" :SortExp="SortExp" :SortBy="SortBy"
                v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata"
                :NorecordfoundText="$t('NoRecordfound')" v-on:ActionButtonClick="actionButtonClick"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:LeftsearchButtonClick="leftCommonSearch">

                <template v-slot:slotdata="{ data }">

                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <td class="text-center single-action" :title="data.row.status_id == 1001 ? 'Active' : 'Inactive'">
                            <div class="cstm-drop-btn">
                                <select v-model="data.row.status_id"
                                    @change="DrpdownUpdateStatus(data.row.status_id, data.row.group_id);"
                                    :class="data.row.status_id == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                    <option value="1001" selected="selected">Active</option>
                                    <option value="1002">Inactive</option>
                                </select>
                            </div>
                        </td>
                    </template>
                </template>
            </tg-list>
        </section>

        <OpenAddCustomFieldPopup v-if="showCustomfieldPopup" @GetCustomNewField="GetCustomNewField"
            :LanguageArrayData="LanguageArray" :Sub_ModuleId="Sub_ModuleId" :ModuleId=ModuleId></OpenAddCustomFieldPopup>

    </div>
</template>

<script>
import Schema from '../LayoutCustomFields/CustomFieldsSchema.js'
import DataService from "../../services/DataService.js";
import OpenAddCustomFieldPopup from '../../components/Common/Modal/AddCustomFieldsPopup.vue';

export default {

    components: {
        OpenAddCustomFieldPopup
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            searchCondition: '',
            TotalRecords: 0,
            CustomlayoutId: '',
            FieldsData: [],
            showCustomfieldPopup: false,
            module_id: '',
            sub_module_id: '',
            newLanguageArray: [],
            LanguageArray: []


        }
    },

    created: function () {
        debugger;
        var vm = this;
        vm.FetchData();
        vm.getLanguageList();
        Schema.bindleftCommonSearchdropdown(vm, "CustomField", "module");
        Schema.bindleftCommonSearchdropdown(vm, "CustomField", "subModule");
        Schema.bindleftCommonSearchdropdown(vm, "CustomField", "Status");

    },
    methods:
    {

        async getLanguageList() {
            var vm = this;
            DataService.GetLanguageList().then((response) => {
                debugger;
                if (response.data.status == "Success") {
                    let obj = { lang_name: '', lang_id: '' }
                    vm.newLanguageArray = response.data.result.DATA;
                    vm.newLanguageArray.forEach(element => {
                        obj = {
                            lang_name: element.LANG_NAME,
                            lang_id: element.LANG_ID
                        };
                        vm.LanguageArray.push(obj);
                    });
                }
            })
        },

        FetchData: function () {
            debugger;

            var vm = this;

            var params = `module_id=${vm.module_id}&sub_module_id=${vm.sub_module_id}&searchCondition=${vm.searchCondition}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`

            DataService.GetCustomUserDefinedCustomFieldListing(params).then(function (response) {
                vm.isLoading = false;
                debugger;
                if (JSON.parse(response.data) != null) {
                    vm.FieldsData = JSON.parse(response.data).data;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'name') {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn,
                            }
                        }
                        if (column.COLUMN_NAME == 'module_name') {
                            column.settings = {

                            }
                        }
                        if (column.COLUMN_NAME == 'sub_module_name') {
                            //
                        }
                        if (column.COLUMN_NAME == 'status_id') {
                            column.settings = {
                                isInSlot: true,
                            }
                        }


                    })
                    if (vm.FieldsData != null) {
                        vm.TotalRecords = vm.FieldsData[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;

                    }
                    else {
                        vm.noRecord = true;
                        vm.FieldsData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }

                }
                else {
                    vm.noRecord = true;
                    vm.FieldsData = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }

                //setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                //setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);

            })
            vm.isLoading = false;
        },

        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Preview':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        leftCommonSearch: function (value) {
            debugger;
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'OpenAddPAge':
                    // vm.OpenAddPAge('Preview');
                    break;
                case 'EditCustomLayout':
                    vm.EditCustomLayout(item);
                    break;
                case 'CloneCustomLayout':
                    // vm.AddCustomLayout(item);
                    break;
                case 'DeleteSingleRecord':
                    vm.commonDeleteData(item.CUSTOM_LAYOUT_ID);
                    break;
            }
        },

        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddCustomFieldLayout":
                    this.AddCustomFieldLayout(event);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },


        AddCustomFieldLayout: function (event) {
            debugger;
            var vm = this;
            vm.showCustomfieldPopup = true;

        },

        CloseAddCustomFieldPoup() {
            debugger;
            var vm = this;
            vm.showCustomfieldPopup = false;
        },

        clickEventOfColumn: function (row, column) {
            debugger;
            if (column.COLUMN_NAME == 'name') {
                // this.$router.push({
                //     name: 'ManageCustomLayout',
                //     params: { CustomlayoutId: row.CUSTOM_LAYOUT_ID }
                // });
            }
        },
        EditCustomLayout: function (id) {

            // var vm = this;
            // if (id.CUSTOM_LAYOUT_ID != null) {
            //     vm.CustomlayoutId = id.CUSTOM_LAYOUT_ID;
            // }
            // vm.$router.push({
            //     path: "/CustomLayout/Manage/" + vm.CustomlayoutId
            // });

        },
        commonDeleteData: function (id) {
            debugger;
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "",
                        subModuleCode: "",
                        refCode: "TALYGEN_CUSTOM_LAYOUT"
                    };

                    // var objectPostString = JSON.stringify(postJSON);
                    // DataService.CommonCommonDelete(objectPostString).then(function (response) {
                    //     var box = "";
                    //     if (response.status == 200) {
                    //         $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                    //         $("#DeleteMultiple").addClass("disabled");
                    //         box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                    //         vm.PageNumber = parseInt(vm.PageNumber) - 1;
                    //         if (vm.PageNumber <= 0) {
                    //             vm.PageNumber = 1;
                    //         }
                    //         vm.FetchData();
                    //     }
                    //     else {
                    //         box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "Success", true, vm.$t("Alert"));
                    //     }
                    // },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        AddCustomLayout: function (ths) {
            debugger;
            var vm = this;
            if (ths.CUSTOM_LAYOUT_ID != null) {
                vm.CustomlayoutId = ths.CUSTOM_LAYOUT_ID;
                vm.showLayoutPopup = true;
            }
            else {
                vm.showLayoutPopup = true;
                vm.CustomlayoutId = null;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },


        CloseAddPopup: function () {
            var vm = this;
            vm.showLayoutPopup = false;
        },


        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },

        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

    }
}
</script>

<style scoped>
.is-visible {
    display: inline-block;
    height: auto;
}
</style>